.progressContainer{display:flex;align-items:center;justify-content:space-between;margin:48px auto 0 auto}.progressContainer .progressGroup{display:block}.progressContainer .progressGroup .progressIcon{display:inline-block;background-color:rgba(0,0,0,0);border:1px solid var(--progress-current-color);border-radius:50%;width:26px;height:26px;margin:3px;color:var(--progress-current-color)}.progressContainer .progressGroup .progressIcon .text{display:inline-block;width:100%;height:100%;border-radius:50%;font-size:12px;font-weight:700;line-height:24px;text-align:center;color:var(--main-text)}.progressContainer .progressGroup .progressIconDone{display:inline-block;background-color:#000;border:1px solid var(--progress-done-color);border-radius:50%;width:26px;height:26px;margin:3px}.progressContainer .progressGroup .progressIconDone .textDone{display:none}.progressContainer .progressGroup .progressIconDone::after{content:"✔";color:#fff;font-size:12px;line-height:24px;text-align:center}.progressContainer .progressGroup .progressLabel{font-weight:400;line-height:24px;color:var(--main-text)}.progressContainer .progressGroup .progressIconUndone{display:inline-block;background-color:rgba(0,0,0,0);border:1px solid var(--progress-undone-color);border-radius:50%;width:26px;height:26px;margin:3px}.progressContainer .progressGroup .progressIconUndone .textUndone{display:inline-block;width:100%;height:100%;border-radius:50%;font-size:12px;font-weight:700;line-height:24px;text-align:center;color:var(--progress-undone-color)}.progressContainer .progressGroup .progressLabelUndone{font-weight:400;line-height:24px;color:var(--progress-undone-color)}.progressContainer .progressBar,.progressContainer .progressBarHidden{display:flex;flex-grow:1;max-width:60px;height:1px;border-width:2px;border-style:solid;color:var(--done-progress-border)}.progressContainer .progressBarHidden{visibility:hidden}